*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background-color: #d2dbdd;
}
.heading_style{
 padding: 30px 0;
 text-align: center;
 font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
 font-size: 13px;
 letter-spacing: 2px;
 font-weight: 500;
 color: #868686;
 background-color: #fff;
 font-size: 1.5rem;
 margin-bottom: 30px; 
}
.cards{
  width: 100%;
  height: auto;
}
.card{
  margin: 6%;
  transition: all 0.4s cubic-bezier(0.175,0.885,0,1);
  background-color: #fff;
  width: 21.25%;
  height: 300px;
  border-radius: 12px;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  float: left;
}
.cards:hover{
  box-shadow: 0px 30px 18px -8px  rgba(0, 0, 0, 0.1);
  transform: scale(1.05,1.05);
}
.card_img{
  height: 235px;
  width: 100%;
  border-top-left-radius:12px;
  border-top-right-radius:12px;
}
.card_info{
  background-color: #fff;
  border-bottom-left-radius:12px;
  border-bottom-right-radius:12px;
  padding: 16px 24px 24px 24px;
  background-color: #fff;
}
.class_catagory{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
}
.card_title{
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: fantasy;
  font-size: 30px;
  background-color: #fff;
  text-transform: capitalize;
}
button{
  padding: 5px 8px;
  font-family: sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  outline: none;
  border: 1px solid black;
  cursor: pointer;
}

@media(max-width:768px){
  *{
    align-items: center;
  }
   .card{
     margin-left: 5px;
     margin-right: 8px;
    transition: all 0.4s cubic-bezier(0.175,0.885,0,1);
    background-color: #fff;
    width: 98%;
    height: 300px;
    border-radius: 12px;
    box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
    float: left;
    margin-bottom: 100px;
   }

}